
@font-face {
  font-family: 'MoskowSans';
  src: url('moskow.ttf');
}

body {
	overflow: hidden;
	font-family: 'MoskowSans', 'Open Sans', sans-serif;
}

.modal-backdrop.show {
	display: none!important;
}

.modal-open .modal-small {
	pointer-events: none;
}

.modal-bottom .modal-content {
	min-height: 23vh!important;
}

.modal-bottom .modal-dialog-centered {
	align-items: flex-end!important;
	min-height: 100%!important;
}

.modal-bottom.fade .modal-dialog {
	transform: translate(0,100px)!important;
}

.modal-bottom.show .modal-dialog {
    transform: none!important;
}

.fix-scroll .modal {
	overflow-y: hidden!important;
}

#searchbox {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	background-color: #fff;
	z-index: 9;
	box-shadow: 0px 7px 10px black;
}

.searchbox-inner {
	margin: 15px 10px;
}

.searchbox-inner .searchbox-input {
	margin: 10px;
}

.switch-button {
	position: absolute;
	bottom: 30px;
	left: calc(50% - 16px);
}

.switch-button img {
	width: 32px;
}

.search-header h5 {
	text-align: center;
	margin-top: 15px;
	margin-bottom: -10px;
}

.station-header {
	font-size: 18px;
}

.station-header span {
	display: inline-block;
	width: 21px;
	height: 21px;
	border-radius: 30px;
	background-color: #000;
	margin-right: 5px;
	text-align: center;
	line-height: 21px;
	font-size: 12px;
	color: #fff;
}

.station-caption {
	font-size: 14px;
	color: #656565;
}

.station-body .btn {
	width: 100%;
	background-color: #d02a31;
	border: 0;
}

.button-info {
	margin-top: 10px;
	background-color: #6d6d6d!important;
}

.enters-map_title {
	margin: 50px auto 25px auto;
}

.help-buttons .btn {
	width: 100%;
	background-color: #d02a31;
	border: 0;
	margin-top: 10px;
}

.chart-block {
	margin-top: 50px;
}

.route-header .station-caption {
	font-size: 12px;
}

.route-header .station-header {
	font-size: 12px;
}

.route-header .station-header span {
	display: inline-block;
	width: 16px;
	height: 16px;
	border-radius: 30px;
	background-color: #000;
	margin-right: 5px;
	text-align: center;
	line-height: 16px;
	font-size: 8px;
	color: #fff;
}

.nav-link {
	padding: 10px!important;
}



